import React, { useEffect, useState } from 'react'
import ReactDOM from 'react-dom/client'
import {
  http,
  Address,
  Hash,
  TransactionReceipt,
  createPublicClient,
  createWalletClient,
  custom,
  stringify,
  getContract,
  parseEther,
  formatEther
} from 'viem'
import { goerli } from 'viem/chains'
import 'viem/window'
import { wl1Contract } from './wl1'
import { defineChain } from 'viem'
import './index.css';
import './styles.css';

//console.log(defineChain);
export const lamina1 = {
  id: 7649,
  name: 'Lamina1',
  network: 'lamina1',
  nativeCurrency: {
    decimals: 18,
    name: 'Lamina 1',
    symbol: 'L1',
  },
  rpcUrls: {
    default: {
      http: ['https://rpc-betanet.lamina1.com/ext/bc/C/rpc'],
      webSocket: ['wss://rpc-betanet.lamina1.com/ext/bc/C/rpc'],
    },
    public: {
      http: ['https://rpc-betanet.lamina1.com/ext/bc/C/rpc'],
      webSocket: ['wss://rpc-betanet.lamina1.com/ext/bc/C/rpc'],
    },
  },
  blockExplorers: {
    default: { name: 'Explorer', url: 'https://explorer.lamina1.com' },
  },
  contracts: {
  },
};


const publicClient = createPublicClient({
  chain: lamina1,
  transport: http(),
})
const walletClient = createWalletClient({
  chain: lamina1,
  transport: custom(window.ethereum!),
})

console.log(getContract);
const wl1 = (getContract as any)({
  address: wl1Contract.address,
  abi: wl1Contract.abi,
  publicClient,
walletClient});

/*
var wl1 = getContract();
wl1.address = wl1Contract.address;
wl1.abi = wl1Contract.abi;
wl1.publicClient = publicClient;
wl1.walletClient = walletClient;
 */
function Example() {
  const [account, setAccount] = useState<Address>()
  const [hash, setHash] = useState<Hash>()
  const [receipt, setReceipt] = useState<TransactionReceipt>()
  const [balance, setBalance] = useState<string>()
  const [currentBlock, setCurrentBlock] = useState<number>(0);
  const [wrapAmount, setWrapAmount] = useState<string>("");
  const [withdrawAmount, setWithdrawAmount] = useState<string>("");
  const [totalSupply, setTotalSupply] = useState<string>("");

  useEffect(() => {
  let isMounted = true;

  const updateBlock = async () => {
    const block = await (publicClient as any).getBlockNumber();
    if (isMounted) {
      setCurrentBlock(block);
    }
  };

  const intervalId = setInterval(async () => {
    await updateBlock();
  }, 1000);

  return () => {
    isMounted = false;
    clearInterval(intervalId);
  };
  }, [publicClient]);

  /* TODO: Get this working with wagmi.
  useEffect(() => {
        const unwatch = watchAccount(account => {
          //console.log('Account changed to:', account);
          // make a new public client, which will trigger the useEffect below
          setAccount(account);
        });

        // Cleanup by calling unwatch to unsubscribe from the account change event
        return () => unwatch();
    }, []);

   */
  useEffect(() => {
    const fetchTotalSupply = async () => {
      const supply = await wl1.read.totalSupply();
      setTotalSupply(supply.toString());
    };

    fetchTotalSupply();

    if (account) {
    wl1.read.balanceOf([account]).then((balanceT:any) => {
      setBalance((balanceT).toString());
    });
    }
  }, [publicClient, currentBlock, account]);

  const connect = async () => {
    const [address] = await (walletClient as any).requestAddresses()
    setAccount(address)

    wl1.read.balanceOf([address]).then((balanceT:any) => {
      setBalance(balanceT.toString());
    });

  }

  const withdraw = async () => {
    if (!account || !balance ) return
    const { request } = await (publicClient as any).simulateContract({
      ...wl1Contract,
      functionName: 'withdraw',
      args: [balance],
      account,
    })
    const hash = await (walletClient as any).writeContract(request)
    setHash(hash)
  }

  useEffect(() => {
    (async () => {
      if (hash) {
        const receipt = await (publicClient as any).waitForTransactionReceipt({ hash })
        setReceipt(receipt)
      }
    })()
  }, [hash, publicClient]);

  const wrap = async () => {
    // Implement the logic to wrap ETH and send to contract address
    // For now, assuming walletClient has a sendETH function
    if(account && wrapAmount) {
      await (walletClient as any).sendTransaction({account: account, to: wl1Contract.address, value: parseEther(wrapAmount)});
    }
  }

return (
  <div className="container">
    <h1>Wrap L1 to make WL1</h1>
    <div>
      <p>Wrapped L1 can be used in a variety of contracts; anything that might want programmatic access to your L1 tokens could prefer L1. WL1 is supported by <a href="https://personacollective.ai">Persona Collective</a> at launch for bidding in the secondary market!</p>
      <p>To turn your Wrapped L1 into native L1, Click the "Withdraw" button below.</p>
      <p>The WL1 Contract charges no fees, but the Lamina1 network does charge gas for all transactions.</p>
    </div>
    { 
      !account ? 
        <button onClick={connect}>Connect Wallet</button>
      : 
        <>
          <div>Connected to account: {account}</div>
          <div>{formatEther(BigInt(totalSupply))} Wrapped L1 in Contract</div>
          <div>{formatEther(balance ? BigInt(balance) : BigInt(0))} Wrapped L1 in Your Account</div>
          <div className="action-group">
            <input 
              type="text" 
              value={wrapAmount}
              placeholder="Amount to Wrap"
              onChange={e => setWrapAmount(e.target.value)}
            />
            <button onClick={wrap}>Wrap</button>
          </div>
          <div className="action-group">
            <button onClick={withdraw}>Withdraw {formatEther(balance ? BigInt(balance) : BigInt(0))} Wrapped L1</button>
          </div>
        </>
    }
  </div>
)


}

ReactDOM.createRoot(document.getElementById('root') as HTMLElement).render(
  <Example />
)

